<template>
  <div class="yourClass">
    <audio-enter :is-show="isShow" @closeAudio="closeAudio" />
  </div>
</template>
<script>
import audioEnter from './audio.vue'
export default {
  components: { audioEnter },
  data() {
    return {
      isShow: true
    }
  },
  methods: {
    closeAudio(data) {
      this.isShow = false
      alert(data)
      // ......
      this.uploadWAVData(data)
    },
    uploadWAVData(wavBlob) {
      // formData
      const formData = new FormData()
      // blobfileNameblobfileformData
      const newbolb = new Blob([wavBlob], { type: 'audio/wav' })
      // 
      const fileOfBlob = new File([newbolb], new Date().getTime() + '.wav')
      formData.append('file', fileOfBlob)
      // axios
      this.$postFile('/visitor/audio/upload', formData)
        .then(response => {
          console.log('', response)
        })
        .catch(error => {
          console.error('', error)
        })
    }
  }
}
</script>
